import { configureStore, createSlice } from '@reduxjs/toolkit'

// 'user'   
let user = createSlice({
    name : 'user',
    initialState : { name : '', age : 20 }, //   
    reducers :{
      changeName(state){ //  ''  
        state.name = ''
    },

//     
    increase(state, action){
        state.age += action.payload
    }
    }
})

//        
export let { changeName, increase } = user.actions

// 'cart'    ()
let cart = createSlice({
    name : 'cart',
    initialState : [
        {id : 1, imgurl:'fruit1.jpg', name : '', count : 2},
        {id : 2, imgurl:'fruit2.jpg', name : '', count : 1},
        {id : 3, imgurl:'fruit3.jpg', name : '', count : 1}
    ],
    reducers : {
     //   1 
        addCount(state, action) {  
            let num = state.findIndex((a) => {
                return a.id === action.payload;
            });
            console.log(num);
            console.log("  "+ action.payload);
            console.log("  "+ state[num].id);
            console.log("  "+ state[num].count);

            state[num].count++;
        },
        //   1  (0   )
        decreaseCount(state, action) {
            let num = state.findIndex((a) => {
            return a.id === action.payload;
            });
            console.log(num);
            if (state[num].count > 0) {
            state[num].count--;
            } else if (state[num].count === 0) {
            alert("   .");
            }
        },
            //   ,    +1,   
        addItem(state, action) {
            let num = state.findIndex((a) => a.id === action.payload.id);
            if (num !== -1) {
            state[num].count++;
            } else {
            state.push(action.payload);
            }
        },
        //   
        deleteItem(state, action) {
            let num = state.findIndex((a) => {
            return a.id === action.payload;
            });
            state.splice(num, 1);
        },
        //   
        sortName(state, action) {
            state.sort((a, b) => (a.name > b.name ? 1 : -1));
        }
    }
})


// cart      
export let { addCount, decreaseCount, addItem, deleteItem, sortName } = cart.actions;


//  Redux  
export default configureStore({
    reducer: {
        user: user.reducer,
        cart: cart.reducer,
    },
});


