import { useState } from "react";
import "./App.css";
import {Navbar, Container, Nav, Button} from 'react-bootstrap'
import data from "./db/fruit";
import Products from "./components/Products";
import { Routes, Route, useNavigate} from 'react-router-dom';
import Detail from "./components/Detail";
import NotFound from "./components/NotFound";
import About from "./components/About";
import Member from "./components/Member";
import Location from "./components/Location";
import Title from "./components/Title";
import Title2 from "./components/Title2";
import data2 from "./db/veggie";
import ComVeggie from "./components/ComVeggie";
import Footer from "./components/Footer";
import axios from 'axios'
import Cart from "./components/Cart";
import Board from "./components/Board";

function App() {

  const [fruit, setFruit] = useState(data);
  let [veggie, setVeggie] = useState(data2);
  let [count, setCount] = useState(1);
  let [input, setInput] = useState("");
  const navigate = useNavigate();

  const sortByName = () => {
    let sortedFruit = [...fruit].sort((a, b) => (a.title > b.title ? 1 : -1));
    setFruit(sortedFruit);    
  };

  const sortByPriceLowToHigh = () => {
    let sortedFruit = [...fruit].sort((a, b) => a.price - b.price);
    setFruit(sortedFruit);    
  };

  const sortByPriceHighToLow = () => {
    let sortedFruit = [...fruit].sort((a, b) => b.price - a.price);
    setFruit(sortedFruit);    
  };

  return (
    <div className="App">
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand onClick={()=> navigate("/")}></Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{ navigate('/')}}></Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/detail/1')}}></Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}></Nav.Link> 
            <Nav.Link onClick={() => { navigate('/about') }}></Nav.Link>
            <Nav.Link onClick={() => { navigate("/board");}}></Nav.Link>
          </Nav>
        </Container>
      </Navbar>
    
      <Routes>
          <Route path="/" element={
            <div>            
              <div className="slider" style={{ backgroundImage: `url(${process.env.PUBLIC_URL + '/img/slider.jpg'})`, }}>
              </div>
              <Title />
              
              <div className="container">
                  <div className="row">
                    <div className="col-md-6" style={{textAlign:"left"}}>
                      {/*   */}
                      <input
                        placeholder=" "
                        //    input  
                        onChange={(e) => setInput(e.target.value)}
                        value={input}
                        style={{
                          padding: "10px",
                          marginLeft: "10px", 
                          borderRadius: "4px",
                          border: "1px solid #ccc",
                          width:"250px",
                          marginRight:"10px"
                        }}
                      />
                    
                    </div>
                    <div className="col-md-6" style={{textAlign:"right"}}>
                      {/* select */}
                      <select   
                        onChange={(e) => {
                          if (e.target.value === "low") sortByPriceLowToHigh();
                          if (e.target.value === "high") sortByPriceHighToLow();
                          if (e.target.value === "name") sortByName();
                        }}
                        style={{
                          padding: "10px",
                          marginLeft: "10px", 
                          borderRadius: "4px", 
                          border: "1px solid #ccc",
                          width:"150px" 
                        }}>
                        <option value=""> </option>
                        <option value="low"> </option>
                        <option value="high"> </option>
                        <option value="name"></option>
                      </select>
                    </div>
                  </div>
                </div>

              <div className="container" style={{ marginTop: "30px" }}>
                  <div className="row">
                      {/* 'input'      */}
                      {/* .filter(...)      
                      .map(...)        */}
                    {fruit
                      .filter((item) =>
                        item.title.toLowerCase().includes(input.toLowerCase()) //  
                      )
                      .map((fruit) => (
                        <Products {...fruit} key={fruit.id} /> //   
                      ))}
                  </div>
              </div>        

              <div className="container">
                <div className="row">
                  <div style={{ textAlign: "center" }}>
                      <Title2 />
                      <Button variant="outline-success"
                        count = {count} onClick={() => {
                        if(count==1){
                          axios.get('https://sinaboro.github.io/react_data/veggie2.json').
                            then((result)=>{
                                let copy6 =[...veggie, ...result.data];
                                setVeggie(copy6);
                                setCount(count + 1);
                        
                            }).catch( () => {
                                alert(" .")
                                setCount(count + 1)
                            }
                            )
                        }else if(count==2){
                          axios.get('https://sinaboro.github.io/react_data/veggie3.json').then((result)=>{
                            let copy9 =[...veggie, ...result.data];
                            setVeggie(copy9);
                            setCount(count + 1);
                            })   
                        }else if(count>=3){
                          alert("  .");  
                        }
                      }}
                      > + 3    </Button> 
                  </div>
                  </div>
              </div>

              <div className="container mt-3">
                <div className="row">
                  {veggie.map((item) => (
                    <ComVeggie {...item} key={item.id} />
                  ))}
                </div>
              </div>
              <Footer /> 
            </div> 
          } />
        
          <Route path="/detail/:paramId" element={<Detail fruit = {fruit} />} />          
          <Route path="/cart" element={<Cart />}/>
          <Route path="/about" element={<About />} >
            <Route path="member" element={<Member />} />
            <Route path="location" element={<Location />} />
          </Route>
          <Route path="/board" element={<Board/>} />
          <Route path="/*" element={ <NotFound /> } />     
        </Routes>                    
        
    </div>
  );
}
export default App;